 .contentoracle-ai_chat_root{
	padding: 0.25rem;
	.contentoracle-ai_chat_conversation{
		height: 10rem;
		overflow-y: auto;
		margin: 0.5rem 0;
		padding: 0.25rem 0.75rem;

		.contentoracle-ai_chat_bubble{
			margin: 0.25rem 0;
			padding: 0.25rem;
			border-radius: 0.5rem;
			display: inline-block;
			max-width: 80%;
			&.contentoracle-ai_chat_bubble_user{
				background-color: #f0f0f0;
				float: right;
			}
			&.contentoracle-ai_chat_bubble_bot{
				background-color: #f0f0f0;
				float: left;
			}
		}

		.contentoracle-ai_chat_bubble::after{
			content: "";
			position: absolute;
			bottom: 0;
			left: 20px; // Adjust this value to position the tail
			width: 0;
			height: 0;
			border: 10px solid transparent;
			border-top-color: #f1f0f0; // Same color as the chat bubble background
			border-bottom: 0;
			border-left: 0;
			margin-left: -5px;
			margin-bottom: -10px;
		}

	}
	.contentoracle-ai_chat_input_container{
		display: flex;
		align-items: center;
		padding: 2px;
		.contentoracle-ai_chat_input{
			flex-grow: 1;	/* grow to fill the space */
			padding: 1rem 0.4rem;
			font-size: var(--wp--preset--font-size--medium);
			border-width: 0;
		}
		.contentoracle-ai_chat_button{
			padding: 1rem;
			font-size: var(--wp--preset--font-size--medium);
			margin-left: 0.25rem;
			border: none;
		}
	}
}